interface Iuser {
  name: string
  password: string
  id: number
  auth: 'admin' | 'visitor'
}

const userData: Array<Iuser> = [
  {
    name: 'admin',
    password: '123456',
    id: 1,
    auth: 'admin',
  },
]

export const Login = (name: string, password: string) => {
  const userInfo: Iuser | undefined = userData.find((item) => {
    return name == item.name && password == item.password
  })
  return userInfo
}
