<template>
  <div class="login">
    <div class="content">
      <div class="title">账户登陆</div>
      <el-form :model="loginForm">
        <el-form-item>
          <el-input v-model="loginForm.name"
                    prefix-icon="el-icon-lock"
                    placeholder="账号：admin 或者 test"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="loginForm.password"
                    prefix-icon="el-icon-lock"
                    placeholder="密码：123456"
                    @keyup.enter="login"></el-input>
        </el-form-item>
        <el-button class="login_btn"
                   @click="login">登陆</el-button>
      </el-form>
    </div>

  </div>
</template>

<script lang='ts'>
import { computed, defineComponent, reactive, toRefs } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { ElMessage } from "element-plus";
import { Login } from "@/common/data/user";
export default defineComponent({
  name: "Home",
  setup() {
    const store = useStore();
    const router = useRouter();
    const isLogin = computed(() => store.state.isLogin);
    isLogin.value && router.replace("/");

    const state = reactive({
      loginForm: {
        name: "admin",
        password: "123456",
      },
    });

    const login = () => {
      // 判断用户名密码
      const userInfo = Login(state.loginForm.name, state.loginForm.password);

      if (!userInfo) {
        return ElMessage.error("用户名或密码错误");
      }

      store.commit("Login");
      store.commit("setUserInfo");
      router.replace("/");
    };

    return {
      ...toRefs(state),
      login,
    };
  },
});
</script>

<style lang="less" scoped>
.login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background: url("../common/image/317546.jpg");
  background-size: cover;
  .content {
    width: 400px;
    height: 300px;
    background-color: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(7px);
    border-radius: 3px;
    text-align: center;
    .title {
      font-size: 28px;
      margin-top: 30px;
      margin-bottom: 30px;
      text-align: center;
      color: #fff;
    }
    /deep/ .el-input {
      width: 320px;
    }
    .login_btn {
      text-align: center;
      background-color: #bfc0c4;
    }
  }
}
</style>